export default{
  data() {
    return {
      order_id: '',
      detail: '',
      good: {
        name: '电子烟电子烟',
        norm: '黑色普通版',
        integral: 999,
        num: 1,
        fare: 100
      }
    }
  },
  onLoad(opt) {
    this.order_id= opt.order_id
    this.getDetail(opt.order_id)
  },
  methods: {
    
    // 获取订单详情
    getDetail(id) {
      let that= this,
        params = {
          url: that.$api.orderDetail,
          data: {
            order_id: id
          }
        }
      that.$httpRequest(params).then(res => {
        console.log(res)
        that.detail= res.data.order
      })
    }
  }
}
